import React from "react";

const User = ({ user, remove }) => {
  const { id, name, email, phone, website } = user;

  return (
    <>
      <div className=" width mx-auto card-me  px-5 py-4 my-3">
        <div>
          <h3 className="py-3">Name: {name}</h3>
          <h4 className="py-3">Email: {email}</h4>
          <h4 className="py-3">Phone: {phone}</h4>
          <h4 className="py-3">Website: {website}</h4>
          <button
            onClick={() => remove(id)}
            className="btn mx-auto btn-sm mt-1 btn-outline-danger"
          >
            Remove
          </button>
        </div>
      </div>
    </>
  );
};

export default User;
